/* ====================== */
.progress-button[data-style="rotate-angle-top"] .progress {
  position: absolute;
  bottom: 100%;
  left: 0;
  width: 100%;
  height: 20px;
  box-shadow: 0 1px 0 #616d7b;
  /* fix the blurriness that causes a gap */
  -webkit-transform: rotateX(90deg);
