.progress-button[data-style="rotate-angle-left"] .progress {
  position: absolute;
  top: 0;
  right: 100%;
  width: 20px;
  height: 100%;
  box-shadow: 1px 0 0 #616d7b;
  /* fix the blurriness that causes a gap */
  -webkit-transform: rotateY(-90deg);
