.animation {
  -webkit-animation: cssAnimation 0.45s 1 ease;
  -moz-animation: cssAnimation 0.45s 1 ease;
  -o-animation: cssAnimation 0.45s 1 ease;
}

@-webkit-keyframes cssAnimation {
  from {
    -webkit-transform: rotate(0deg) scale(0.89) skew(0deg) translate(0px);
    opacity: 0.3;
  }
  to {
    -webkit-transform: rotate(0deg) scale(1) skew(0deg) translate(0px);
    opacity: 1;
  }
}

@-moz-keyframes cssAnimation {
  from {
    -moz-transform: rotate(0deg) scale(0.89) skew(0deg) translate(0px);
    opacity: 0.3;
  }
  to {
    -moz-transform: rotate(0deg) scale(1) skew(0deg) translate(0px);
    opacity: 1;
  }
}

@-o-keyframes cssAnimation {
  from {
    -o-transform: rotate(0deg) scale(0.89) skew(0deg) translate(0px);
    opacity: 0.3;
  }
  to {
    -o-transform: rotate(0deg) scale(1) skew(0deg) translate(0px);
    opacity: 1;
  }
}

